.trailers{
  margin-top: 53px;

  &__link{
    margin-top: 12px;
    margin-right: 7px;
  }

  &__content{
    margin-top: 62px;
  }

  &__video{
    border-radius: 10px;
    max-width: 1430px;
    max-height: 765px;
  }

  &__info{
    margin-top: 10px;
  }

  &__block{
    row-gap: 15px;
    column-gap: 35px;
  }

  &__list{
    margin-top: 10px;
    column-gap: 28px;
  }

  &__reactio{
    gap: 10px;
  }

  .trailers__swiper-two{
    max-height: 350px;
  }

  .trailers__swiper-two .trailers__swiper-scrollbar{
    position: relative;
    margin-top: 29px;
    margin-bottom: 20px;
    min-height: 7px;
  }

  &__name{
    margin-top: 5px;
  }

  .swiper-scrollbar-drag{
    background-color: var(--cerulean-blue);
  }

  &__block-like{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  &__slide-link{

    transition: color 0.3s ease-in-out;

    &:hover{
      color: var(--lemon);
    }

    &:focus{
      color: var(--lemon);
    }

    &:active{
      color: var(--cerulean-blue);
    }
  }

  &__bot{
    margin-top: 56px;
  }

  &__item-video{
    grid-column: 3 span;
  }
}


@media (max-width: 1400px){

  .trailers{

    &__content{
      margin-top: 19px;
    }

    &__video{
      max-height: 455px;
    }

    &__info{
      margin-top: 6px;
    }

    .trailers__swiper-two{
      max-height: 200px;
    }
  }

}

@media (max-width: 800px){

  .trailers{

    margin-top: 43px;

    &__video{
      max-height: 353px;
    }
  }
}


@media (max-width: 525px){

  .trailers{

    &__menu{
      flex-direction: column;
    }

    .list-social{
      display: none;
    }

    &__video{
      max-height: 222px;
    }
  }
}
